ри пропадании внешнего питания и автоматический заряд резервного аккумулятора при работе от внешнего питания. Предусмотрены выходы постоянного напряжения +3,3 В и +(7…9) В для питания внешних устройств и датчиков.")]),_c('span',[_vm._v("Широкие телеметрические возможности")]),_c('p',[_vm._v("Контроллер поддерживает: "),_c('ul',[_c('li',[_vm._v("определение состояния трёх дискретных внешних датчиков с заданием режимов работы каждого входа (замкнуто/разомкнуто или детектор импульсов);")]),_c('li',[_vm._v("измерение частоты на любом из дискретных входов (до 4 кГц);")]),_c('li',[_vm._v("счетчики импульсов на любом из дискретных входов (24 разряда, 16 777 216 значений);")]),_c('li',[_vm._v("определение состояния трёх аналоговых внешних датчиков (напряжение 0…2,5 В, 8 бит) с выбираемым усреднением каждого входа 3, 10, 30 или 90 секунд;")]),_c('li',[_vm._v("определение состояния трёх цифровых (12 бит) датчиков уровня топлива , совместимых по протоколу с датчиками LLS фирмы Омникомм;")]),_c('li',[_vm._v("поддержка последовательного интерфейса RS 485 для работы с внешними устройствами (текущая версия поддерживает внешний считыватель RFID бесконтактных карт MIFARE для идентификации водителей);")]),_c('li',[_vm._v("управление внешними исполнительными устройствами (три выхода на включение/выключение внешних устройств при токе до 1 А и напряжении до 30 В).")])]),_vm._v(" Определение несанкционированного вмешательства"),_c('br'),_vm._v(" Обеспечивается определение неисправностей навигационной антенны : обрыв или короткое замыкание (только для исполнения «GPS»), пропадания внешнего питания, обрыв сигнала «Зажигание», извлечение SIM карты.")]),_c('span',[_vm._v("Надежное и экономное накопление и доставка данных")]),_c('p',[_vm._v("Встроенная в контроллер энергонезависимая память на 62 000 отчетов (точек маршрута) обеспечивает автономную работу контроллера в режиме «черного ящика» при отсутствии связи с диспетчерским центром.")]),_c('p',[_vm._v("При записи точек маршрута используется адаптивный, гибко настраиваемый пользователем алгоритм по изменению курса (направления движения), скорости, пройденному расстоянию, времени, изменению состояния датчиков и т.д., позволяющий не пропустить важных событий и подробно «прорисовать маршрут» без избыточного использования памяти и GPRS трафика.")]),_c('p',[_vm._v("При доставке данных о местоположении и состоянии объекта через сеть Интернет с использованием технологии пакетной передачи данных ( GPRS ) обеспечивается гарантированная доставка отчетов (с надежным механизмом подтверждений «поверх» TCP/IP) с шифрованием передаваемых данных (длина ключа 128 бит). При этом обеспечивается сжатие передаваемых по GPRS данных, что позволяет существенно (в среднем до 70%) снизить количество передаваемой по GPRS информации и, следовательно, дополнительно снизить расходы на оплату трафика.")]),_c('p',[_vm._v("Контроллер поддерживает протокол передачи данных для использования с серверным ПО Wialon . Возможна реализация других протоколов для интеграции в диспетчерские системы сторонних производителей.")]),_c('span',[_vm._v("Удобство установки и эксплуатации")]),_c('p',[_vm._v("Для удобства эксплуатации контроллера предусмотрены:")]),_c('ul',[_c('li',[_vm._v("удобная конфигурация с помощью специальной программы с поддержкой сохранения профилей в файлы и расширенной диагностикой контроллера и внешних устройств: отображается подробная технологическая информация о состоянии внутренних узлов контроллера и состоянии подключенных к нему датчиков и устройств;")]),_c('li',[_vm._v("светодиодная индикация режимов работы : 5 индикаторов упрощают процесс установки и диагностики неисправностей ;")]),_c('li',[_vm._v("удаленная конфигурация параметров и режимов работы через GPRS или SMS ;")]),_c('li',[_vm._v("удаленное обновление версий встроенного ПО через TCP / IP соединение по GPRS в фоновом режиме (без прерывания полноценной работы контроллера);")]),_c('li',[_vm._v("накопление статистической информации о работе контроллера: время работы, количество включений, установок/извлечений SIM карты, входящих/исходящих SMS, объем входящего/исходящего GPRS трафика и др.")])]),_c('span',[_vm._v("Поддержка, развитие, гарантийное обслуживание")]),_c('p',[_vm._v("Команда разработчиков ООО «Аруснави» обеспечит всю необходимую техническую поддержку по применению контроллера, а также готова реализовать нужные в том или ином проекте новые функции. Обеспечивается также оперативное гарантийное и постгарантийное обслуживание.")])])])
